@use "reset";
@use "variable" as var;
@use "mixin" as mx;
@use "btn";


body{
    width: 100%;
    min-height: 100%;
    background: var.$color-bg;
    font-size: 16px;
    @include mx.mq(sp){
        font-size: 14px;
    }
    color: var.$color-key;
    font-family: "Inter",
    "Helvetica Neue",
    Arial,
    "Hiragino Kaku Gothic ProN",
    "Hiragino Sans",
    "BIZ UDPGothic",
    Meiryo,
    sans-serif;
    letter-spacing: 0.1ex;

}
img{
    width: 100%;
    object-fit: contain;
}
.container{
    position: relative;
    top: 0;
}
.side-border{
    position: fixed;
    z-index: -1;
    top: 0;
    width: 100%;
    height: 100lvh;
    &_left{
        display: block;
        position: absolute;
        top: 0;
        left: 40px;
        width: 1px;
        height: 100vh;
        background-color: var.$color-gray;
        @include mx.mq(sp){
            left: 20px;
        }
    }
    &_right{
        display: block;
        position: absolute;
        top: 0;
        right: 40px;
        width: 1px;
        height: 100vh;
        background-color: var.$color-gray;
        @include mx.mq(sp){
            right: 20px;
        }
    }
}
.header{
    position: fixed;
    top: 0;
    z-index: 1;
    width: 100%;
    height: auto;
    background: #fff;
    border-top: 1px solid var.$color-key;
    border-bottom: 1px solid var.$color-key;
    &-inner{
        margin: 0 40px;
        border-right: 1px solid var.$color-key;
        border-left: 1px solid var.$color-key;
        @include mx.mq(sp){
            margin: 0 20px;
            padding: 24px;
        }
    }
    &-column{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include mx.mq(sp){
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
        }
    }
    &-logo{
        width: 242px;
        padding: 0 0 0 40px;
        @include mx.mq(sp){
            width: 160px;
            padding: unset;
        }
        svg{
            width: 100%;
        }
        
    }
    &-navi{
        display: flex;
        justify-content: flex-start;
        background: #fff;
        @include mx.mq(sp){
            display: none;
            position: fixed;
            z-index: 3;
            top: 70px;
            width: 92%;
            height: 100lvh;
            left: 0;
            right: 0;
            margin: auto;
            flex-direction: column;
        }
    }
    &-sns{
        display: grid;
        place-content: center;
        border-left: solid 1px var.$color-key;
        width: 106px;
        margin: 0 0 0 40px;
        a{
            display: block;
            width: 28px;
            height: 28px;
        }
    }
}
.navi{
    display: flex;
    gap: 40px;
    padding: 30px 20px;
    @include mx.mq(sp){
        flex-direction: column;
        justify-content: center;

    }
    &-item{
        text-align: center;
        a{
            display: block;
        }
        &_en{
            margin: 0 0 8px 0;
            font-size: 18px;
        }
        &_ja{
            font-size: 14px;
        }
    }
}
.main-visual{
    width: 100%;
    max-width: 1260px;
    margin: 164px auto 154px;
    @include mx.mq(sp){
        padding: 0 40px;
    }
    &_photo{
        transform: scale(1.0);
    }
    &_logo{
        max-width: 640px;
        margin: 0 auto 56px;
        @include mx.mq(sp){
            margin: 0 auto 16px;
            width: calc(208px / 320px * 100%);
        }
    }
}

.section{
    &-titles{
        width: fit-content;
        &_head{
            font-weight: 600;
            font-size: 56px;
            @include mx.mq(sp){
                font-size: 24px;
            }
            color: #fff;
            text-shadow: 0px 0px 5px rgba($color: #000000, $alpha: 0.4);
        }
        &_sub{
            font-size: 32px;
            @include mx.mq(sp){
                font-size: 16px;
            }
            font-weight: bold;
            color: #fff;
            letter-spacing: .3ex;
            text-shadow: 0px 0px 5px rgba($color: #000000, $alpha: 0.4);
        }
    }
    &-text{
        font-size: 16px;
        margin: 0 0 32px 0;
    }
    &-list{
        padding: 0 0 0 40px;
        margin: 0 0 32px 0;
        border-left: 1px solid var.$color-gray;
    }
    &-detail{
        &_title{
            font-size: 24px;
            margin: 0 0 24px 0;
        }
        &_text{
            font-size: 16px;
            padding: 0 0 8px 40px;
            border-left: 1px solid var.$color-gray;
        }
    }
}
.business{
    max-width: 1280px;
    margin: 0 auto 156px;
    &-thumbs{
        position: relative;
        top: 0;
        width: 960px;
        margin: 0 auto 64px;
        @include mx.mq(sp){
            width: 100%;
        }
    }
    &-titles{
        position: absolute;
        bottom: 40px;
        left: 40px;
    }
    &-details{
        width: 960px;
        margin: 0 auto;
        padding: 0 0 0 40px;
    }
}
.mechannical{
    margin: 0 80px 156px 80px;
    &-column{
        display: flex;
        align-items: center;
        gap: 80px;
        @include mx.mq(sp){
            flex-direction: column;
        }
        &_thumb{
            flex-basis: 60%;
            position: relative;
            top: 0;
            aspect-ratio: 16 / 9;
        }
        &_titles{
            position: absolute;
            bottom: 40px;
            left: 40px;
        }
    }
}
.recruit{
    margin: 0 80px 156px 80px;
    &-column{
        display: flex;
        flex-direction: row-reverse;
        gap: 80px;
        @include mx.mq(sp){
            flex-direction: column;
        }
        &_thumb{
            flex-basis: 60%;
            position: relative;
            top: 0;
        }
    }
    &-titles{
        position: absolute;
        top: 40px;
        left: 40px;
    }
}
.footer{
    border-top: 1px solid var.$color-key;
    border-bottom: 1px solid var.$color-key;
    &-inner{
        margin: 0 40px;
        border-right: 1px solid var.$color-key;
        border-left: 1px solid var.$color-key;
        @include mx.mq(sp){
            margin: 0 20px;
        }
    }
    &-column{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        border-bottom: 1px solid var.$color-key;
        @include mx.mq(sp){
            grid-template-columns: 1fr;
            grid-template-rows: repeat(2, auto);
        }
        &:last-child{
            border-bottom: none;
        }
    }
    &-company{
        padding: 32px 0  56px 32px;
        border-right: 1px solid var.$color-key;
        @include mx.mq(sp){
            padding: 32px 20px  56px 20px;
            border-right: none;
            border-bottom: 1px solid var.$color-key;
        }
    }
    &-maps{
        position: relative;
        top: 0;
        @include mx.mq(sp){
            height: 360px;
        }
        iframe{
            position: absolute;
            top: 0;
            width: 99.99%;
            height: 100%;
        }
    }
    &-detail{
        display: flex;
        flex-wrap: wrap;
        row-gap: 16px;
        @include mx.mq(sp){
            flex-direction: column;
        }
        &_title{
            font-weight: 600;
            flex-basis: 25%;
        }
        &_content{
            flex-basis: 70%;
        }
    }
    &-contact{
        display: flex;
        align-items: center;
        gap: 56px;
        padding: 32px;
        border-right: 1px solid var.$color-key;
        @include mx.mq(sp){
            border-right: none;
            gap: 16px;
            padding: 24px 0;
            flex-direction: column;
            border-bottom: 1px solid var.$color-key;
        }
        &_heading{
            font-size: 32px;
            font-weight: 600;
        }
    }
    &-copy{
        display: grid;
        justify-content: center;
        align-items: center;
        small{
            font-size: 16px;
            @include mx.mq(sp){
                font-size: 14px;
                padding: 16px 0;
            }
        }
    }
}
.company{
    &-head{
        color: var.$color-key;
        text-shadow: none;
        font-size: 48px;
        @include mx.mq(sp){
            font-size: 32px;
        }
    }
    &-sub{
        color: var.$color-key;
        text-shadow: none;
        margin: 0 0 32px 0;
    }
}